:root{
    --darkgradientPurple: radial-gradient(at 47% 33%, hsl(284.75, 66%, 17%) 0, transparent 59%);
    --darkgradientPurple2: radial-gradient(at 82% 65%, hsl(284.75, 66%, 17%) 0, transparent 55%);
    --gradientPurple: linear-gradient(311deg, rgba(37,22,125,1) 0%, rgba(111,16,107,1) 82%);
    --darkgradientPurpleLineal: linear-gradient(311deg, rgba(26,10,57,1) 0%, rgba(43,9,47,1) 54%, rgba(26,10,57,1) 100%);
    --lightGradientWhite: linear-gradient(311deg, rgb(166 218 255) 0%, rgba(208,235,255,1) 50%, rgb(231 245 255) 100%);
} 